-50 disabled:cursor-not-allowed;\n}\n\n.btn-danger {\n  @apply inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 disabled:opacity-50 disabled:cursor-not-allowed;\n}\n\n/* Animation classes */\n.fade-in {\n  animation: fadeIn 0.2s ease-in-out;\n}\n\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    transform: translateY(-4px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n/* Invoice preview styles */\n.invoice-preview {\n  background: white;\n  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);\n}\n\n.invoice-preview table {\n  border-collapse: collapse;\n  width: 100%;\n}\n\n.invoice-preview th,\n.invoice-preview td {\n  border: 1px solid #e5e7eb;\n  padding: 8px 12px;\n  text-align: left;\n}\n\n.invoice-preview th {\n  background-color: #f9fafb;\n  font-weight: 600;\n}\n\n.invoice-preview .text-right {\n  text-align: right;\n}\n\n/* Loading spinner */\n.spinner {\n  border: 2px solid #f3f3f3;\n  border-top: 2px solid #3498db;\n  border-radius: 50%;\n  width: 20px;\n  height: 20px;\n  animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n/* Responsive adjustments */\n@media (max-width: 1024px) {\n  .three-pane-layout {\n    flex-direction: column;\n  }\n  \n  .left-panel,\n  .right-panel {\n    width: 100% !important;\n    max-height: 300px;\n    overflow-y: auto;\n  }\n  \n  .center-panel {\n    flex: 1;\n    min-height: 400px;\n  }\n}\n"],"sourceRoot":""}]);
